.pdp-thumbnail-slider .slick-slide {
  width: 50px; }

@media (min-width: 768px) {
  .pdp-thumbnail-slider {
    width: 380px;
    margin: 0 auto; } }

.pdp-thumbnail-slider .pdp-thumbnail-image {
  width: 50px;
  height: 50px;
  object-fit: cover; }

.pdp-thumbnail-slider * {
  outline: none; }

.pdp-thumbnail-slider .slick-current .pdp-thumbnail-image {
  border: 4px solid #7dc242; }

@media (max-width: 767px) {
  .pdp-thumbnail-slider .slick-arrow {
    display: none !important; } }

.pdp-main-slider .slick-slide img {
  height: 300px; }
  @media (min-width: 768px) {
    .pdp-main-slider .slick-slide img {
      height: 530px;
      overflow: hidden; } }

@media (min-width: 768px) {
  .pdp-main-slider .slick-arrow {
    display: none !important; } }
