.primary-header {
  box-shadow: 0px 1px 4px 0 rgba(0, 0, 0, 0.08);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
  background-color: #ffffff;
  transition: top 0.3s; }

.header-top {
  background: #000;
  text-align: center;
  padding: 5px; }
  .header-top-text {
    font-size: 12px;
    line-height: 22px;
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    font-weight: 300; }

.header-nav {
  display: flex;
  justify-content: space-between;
  padding: 12px 0;
  align-items: center; }

/* Logo */
.header-logo__img {
  width: 60px;
  height: auto; }
  @media (min-width: 768px) {
    .header-logo__img {
      width: 75px; } }

.nav-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
  display: flex; }

.nav-item:not(:last-child) {
  margin-right: 2rem; }

.nav-item.nav-icon-item {
  margin-right: 1rem; }

@media (max-width: 767px) {
  .nav-item:not(.show-mobile) {
    display: none; } }

/* NavLink */
.nav-link {
  color: #000000;
  display: block;
  padding: 0.4rem 0;
  text-transform: uppercase;
  font-size: 12px;
  position: relative; }
  @media (max-width: 767px) {
    .nav-link {
      font-size: 16px; } }
  .nav-link.active {
    font-weight: 900;
    color: #000000; }
    .nav-link.active.primary {
      color: #7dc242; }
  .nav-link:hover {
    transition: all 0.3s;
    text-decoration: none;
    color: #7dc242; }

.nav-right {
  display: flex;
  align-items: center; }

/* Menu-toggler */
.nav-toggler {
  display: inline-block;
  cursor: pointer;
  margin-left: 1rem; }
  @media (min-width: 768px) {
    .nav-toggler {
      display: none; } }
  .nav-toggler .bar {
    position: relative;
    width: 18px;
    height: 2px;
    background-color: #000000; }
    .nav-toggler .bar:not(:last-child) {
      margin-bottom: 4px; }

.cart-added-count {
  position: absolute;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #7dc242;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  color: #ffffff;
  top: 0;
  right: -0.75rem; }
