.product-image {
  height: 200px;
  width: 100%;
  object-fit: cover; }
  @media (min-width: 768px) {
    .product-image {
      height: 378px; } }

@media (max-width: 767px) {
  .products-wrapper {
    display: flex;
    flex-wrap: wrap; } }

.product-item {
  text-decoration: none;
  color: #000; }
  @media (max-width: 767px) {
    .product-item {
      width: calc(50% - 0.25rem);
      margin-bottom: 1rem; }
      .product-item:nth-child(2n + 1) {
        margin-right: 0.25rem; }
      .product-item:nth-child(2n + 2) {
        margin-left: 0.25rem; } }
  @media (min-width: 768px) {
    .product-item {
      margin-right: 1rem; } }

@media (min-width: 768px) {
  .product-item-info {
    padding-left: 0.5rem; } }

.product-name {
  color: #000000;
  /* font-weight         : $font-wt;*/
  font-size: 12px;
  line-height: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 0.5rem; }
  @media (min-width: 768px) {
    .product-name {
      font-size: 14px;
      line-height: 22px; } }
  @media (min-width: 992px) {
    .product-name {
      font-size: 16px;
      line-height: 26px; } }

