.footer-main {
  background-color: #000000;
  color: #ffffff;
  font-size: 12px; }
  @media (max-width: 767px) {
    .footer-main {
      padding: 0 1.5rem; } }

.footer-top {
  padding: 2rem 0; }
  @media (min-width: 768px) {
    .footer-top {
      padding: 3rem 0; } }

.footer-title {
  text-transform: uppercase;
  font-size: 12px;
  margin-bottom: 0.5rem; }

.footer-list li,
.footer-list a {
  color: #ffffff; }
  .footer-list li:hover,
  .footer-list a:hover {
    color: #ffffff; }

.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  padding: 1.5rem 1rem; }

.footer-secondary-text {
  color: #ffffff;
  opacity: 0.5;
  margin-bottom: 0;
  font-weight: 400; }

.footer-social {
  margin-top: 2.6rem; }

.footer-social-link {
  margin-right: 1rem; }

.newslater-input {
  font-size: 12px;
  font-weight: 700; }

@media (max-width: 767px) {
  .footer-top-right {
    order: -1;
    margin-bottom: 2rem; } }
