.container-fluid {
  width       : 100%;
  margin-right: auto;
  margin-left : auto;
  max-width   : 1440px;

  padding-right: 0.5rem;
  padding-left : 0.5rem;

  @include bp(md) {

    padding-right: 3rem;
    padding-left : 3rem;
  }
}

