.tabs {
  border: 1px solid #c8c8c8; }

.tab-body {
  padding: 1rem; }

.tab-nav {
  background-color: #f1f1f1; }

.tab-nav ul {
  list-style-type: none;
  display: flex;
  width: 100%;
  padding: 0;
  margin: 0; }

.tab-nav ul li {
  display: inline-flex;
  flex: 1; }

.tab-nav ul li a {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 0.6rem;
  text-decoration: none;
  text-transform: uppercase;
  border: 1px solid #c8c8c8;
  border-top-color: #ffffff;
  font-weight: 700;
  color: #000000; }
  @media (min-width: 768px) {
    .tab-nav ul li a {
      padding: 1rem; } }
  .tab-nav ul li a.active {
    background-color: #ffffff;
    border-color: #ffffff;
    color: #7dc242; }

.tab-panes .active {
  display: block; }

.tab-panes .inactive {
  display: none; }
