.custom-radio {
  display: inline-block; }
  .custom-radio input {
    height: 0;
    width: 0;
    opacity: 0; }
  .custom-radio input:checked + .radio-value {
    border: 3px solid #7dc242; }
  .custom-radio input[disabled] + .radio-value {
    background-color: #c8c8c8; }
  .custom-radio .radio-value {
    width: 34px;
    height: 34px;
    border: 1px solid #c8c8c8;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    font-size: 14px;
    font-weight: 500;
    user-select: none; }
  .custom-radio:not(:last-child) {
    margin-right: 0.75rem;
    margin-bottom: 0.75rem; }
