.color-checkbox {
  display: inline-block; }
  .color-checkbox input {
    height: 0;
    width: 0;
    opacity: 0; }
  .color-checkbox input:checked + .color-label::after {
    position: absolute;
    content: "";
    width: 30px;
    height: 30px;
    border: 2px solid #7dc242;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }
  .color-checkbox .color-label {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #000;
    display: inline-flex;
    position: relative; }
  .color-checkbox:not(:last-child) {
    margin-right: 0.75rem;
    margin-bottom: 0.75rem; }
